<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Attack DoS</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-select
                :items="pages"
                v-model="selected"
                label="Select Page"
                dense
                outlined
              ></v-select>
              <v-slider
                v-model="attackers"
                :min="20"
                :max="2000"
                thumb-label
              ></v-slider>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn v-if="!attacking" @click="Launch" color="primary">Launch</v-btn>
            <v-btn v-else @click="Stop" outlined color="primary">stop</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar :timeout="2000" v-model="snackbar.active">
      {{ snackbar.text }}
      <v-btn icon color="primary" text @click="snackbar.active = false">
        <v-icon>
          {{ icon.cancel }}
        </v-icon>
      </v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
import { mdiAccount, mdiPhone, mdiFile, mdiImage, mdiCancel } from '@mdi/js'

function randstr(length, onlyDigits = false) {
   var result           = '';
   let characters;
   if(onlyDigits)
    characters          = '0123456789';
   else
    characters           = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
   var charactersLength = characters.length;
   for ( var i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
   }
   return result;
}

export default {
  methods: {
    Snack (message) {
      // snackbar for error
      this.snackbar.text = message;
      this.snackbar.active = true;
    },
    Launch () {
      if(this.selected === null) {
        this.Snack('No page selected');
        return;
      }
      this.attacking = true;
      var url = this.selected === 'No Captcha' ? '/nocaptcha/signup' : '/captcha/signup';
      for(var i=0; i < this.attackers; i++) {
        this.attackerIds.push(setInterval(() => {
          this.axios.post(url, {
            name: randstr(10),
            roll: randstr(10),
            phone: randstr(10),
            captcha: randstr(10),
            hash: randstr(10)
          }).catch(() => {
            this.Snack(`Attack Success! Server slowed`);
          });
        }, 10));
      }
    },
    Stop () {
      this.attacking = false;
      this.attackerIds.forEach((v) => {
        clearInterval(v);
      });
      this.attackerIds = [];
    }
  },
  data: () => ({
    icon: {
      account: mdiAccount,
      file: mdiFile,
      phone: mdiPhone,
      image: mdiImage,
      cancel: mdiCancel
    },
    pages: ['No Captcha', 'Captcha'],
    selected: 'No Captcha',
    attacking: false,
    snackbar: {
      active: false,
      text: null
    },
    attackers: 20,
    attackerIds: []
  })
}
</script>