<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Registration form</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field label="Name" v-model="input.name" :prepend-icon="icon.account" type="text" />
              <v-text-field label="Enrollment Number" v-model="input.roll" :prepend-icon="icon.file" type="text" />
              <v-text-field label="Phone Number" v-model="input.phone" :prepend-icon="icon.phone" type="text" />
              <template v-if="$route.params.captcha === 'captcha'">
                <v-container v-html="captchaImage" width="200">
                </v-container>
                <v-text-field label="Captcha Text" v-model="input.captcha" :prepend-icon="icon.image" type="text" />
              </template>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn @click="Submit" color="primary">Register</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar :timeout="2000" v-model="snackbar.active">
      {{ snackbar.text }}
      <v-btn icon color="primary" text @click="snackbar.active = false">
        <v-icon>
          {{ icon.cancel }}
        </v-icon>
      </v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
import { mdiAccount, mdiPhone, mdiFile, mdiImage, mdiCancel } from '@mdi/js'

export default {
  name: "Registration",
  methods: {
    Snack (message) {
      // snackbar for error
      this.snackbar.text = message;
      this.snackbar.active = true;
    },
    GetCaptcha () {
      this.axios.get('/captcha').then((response) => {
        this.captchaImage = response.data.data;
        this.input.hash = response.data.hash;
      });
    },
    async Submit () {
      for(var [key, value] of Object.entries(this.input)) {
        if(key === 'captcha' && this.$route.params.captcha !== 'captcha')
          continue;
        if(value === null || value.trim().length === 0) {
          this.Snack(`Invalid input for ${ key }`);
          return;
        }

        if(this.$route.params.captcha === 'captcha') {
          // for captcha
            this.axios.post('/captcha/signup', this.input, {
              responseType: 'json'
            }).then((response) => {
              if(response.status === 200) {
                this.$router.push({ name: 'dashboard', params: { profile: response.data } });
              }
            }).catch((error) => {
              if(error.response.status === 403) {
                this.GetCaptcha();
                this.Snack(error.response.data.error);
              }
            });
        } else {
          // for no-captcha
          this.axios.post('/nocaptcha/signup', this.input, {
            responseType: 'json'
          }).then((response) => {
            if(response.status === 200) {
              this.$router.push({ name: 'dashboard', params: { profile: response.data } });
            }
          }).catch((error) => {
            if(error.response.status === 403) {
              this.Snack(error.response.data.error);
            }
          });
        }
      }
    }
  },
  data: () => ({
    icon: {
      account: mdiAccount,
      file: mdiFile,
      phone: mdiPhone,
      image: mdiImage,
      cancel: mdiCancel
    },
    input: {
      name: null,
      roll: null,
      phone: null,
      captcha: null,
      hash: null
    },
    snackbar: {
      active: false,
      text: null
    },
    captchaImage: null
  }),
  mounted () {
    this.GetCaptcha();
  }
};
</script>